
// Properties that stores colors and shades so that the can be reused
$headerColor: #2f3e46;
$textColor: #ffffff;
$mainDivColor: #354f52;
$bodyBackground: #deebe5;
$btnHover: #b3d8cb;
$boxshadow: 5px 5px 20px 1px rgba(0, 0, 0, 0.164);

//The properties above are used in the elements below to get the right color and shadow to the elements
body{
    font-family: 'Quicksand', sans-serif;
    background-color: $bodyBackground;
    color: $textColor;
    margin: 0 !important;
}

a{
    color: $textColor;
}

h1{
    margin-left: 23%;
}

header{
    background-color: $headerColor;
    padding: 2%;
    box-shadow: $boxshadow;
}

h2{
    margin-left: 2%;
}

ul{
    margin-left: -4%;
}

li{
    padding-top: 2%;
    padding-bottom: 2%;
    padding-left: 2%;
    list-style: none;
}

.delete{
    color: red;
    margin-left: 5%;
    cursor: pointer;
}

.update{
    color: rgb(134, 134, 248);
    cursor: pointer;
    margin-left: 5%;
}

li:nth-child(odd){
    background-color: $headerColor;
}
li:nth-child(even){
    background-color: #2f3e465b;
}

.listHead li{
    display: inline-block;
    padding: 1%;
    background-color: rgba(255, 0, 0, 0);
}
div {
    padding: 1% 1% 3% 1%;
    background-color: $mainDivColor;
    width: 50%;
    margin: auto;
    margin-top: 3%;
    box-shadow: $boxshadow;
    border-radius: 2px;
}
h3{
    margin-left: 30%;
    font-size: 180%;
}
form{
    margin-left: 30%;
    font-size: 130%;
}
input{
    width: 50%;
    margin-bottom: 4%;
    padding: 1.2%;
    border-style: none;
    border-radius: 2px;
}
.btn{
    padding: 2%;
    border-radius: 5px;
    border-style: none;
    margin-top: 2%;
    width: 30%;
    background-color: $textColor;
}

.btn:hover{
    background-color: $btnHover;
}


//media query is set to 800px to make the page responsive so it can be used on both big and small screen sizes
@media(max-width: 800px){
    body{
        font-size: 100%;
    }
    div{
        width: 100%;
        padding: 0!important;
    }
    input{
        width: 80%;

    }
    form{
        margin-left: 7%;
    }
    h1{
        font-size: 80%;
        margin-left: 1%;
    }
}